// src/api/authService.ts
import axios from 'axios';
// Base URL pour les endpoints
const API_URL = 'http://localhost:3000/auth';
const API_URL_PROTECTED = 'http://localhost:3000/api';
// Crer une instance Axios pour configurer les en-ttes communs
export const apiClient = axios.create({
    baseURL: API_URL,
    headers: { 'Content-Type': 'application/json', }
});
// Ajouter un intercepteur pour inclure automatiquement le token JWT
apiClient.interceptors.request.use((config) => {
    const token = localStorage.getItem('token');
    if (token) {
        config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
}, (error) => Promise.reject(error));
// Fonction pour grer les erreurs (sans oprateur optionnel)
const handleError = (error) => {
    const errorMessage = error.response && error.response.data ? error.response.data : error.message;
    console.error('Erreur API :', errorMessage);
    throw errorMessage;
};
// Service d'inscription
export const register = async (Data) => {
    try {
        console.log(Data); // Affiche le fichier dans les logs
        const response = await apiClient.post('/register', Data);
        return response.data;
    }
    catch (error) {
        handleError(error);
    }
};
// Service de connexion
export const login = async (data) => {
    try {
        const response = await apiClient.post('/login', data);
        return response.data;
    }
    catch (error) {
        handleError(error);
    }
};
// Mthode : demander un lien de rinitialisation de mot de passe
export const requestPasswordReset = async (email) => {
    try {
        const response = await apiClient.post("/request-password-reset", { email });
        return response.data;
    }
    catch (error) {
        if (axios.isAxiosError(error)) {
            const errorMessage = error.response && error.response.data
                ? error.response.data.message
                : "Erreur lors de la demande de rinitialisation.";
            throw new Error(errorMessage);
        }
        throw new Error("Une erreur inconnue s'est produite.");
    }
};
// Mthode : rinitialiser le mot de passe avec un token
export const resetPassword = async (token, newPassword) => {
    try {
        const response = await apiClient.post("/reset-password", {
            token,
            newPassword,
        });
        return response.data;
    }
    catch (error) {
        if (axios.isAxiosError(error)) {
            const errorMessage = error.response && error.response.data
                ? error.response.data.message
                : "Erreur lors de la rinitialisation du mot de passe.";
            throw new Error(errorMessage);
        }
        throw new Error("Une erreur inconnue s'est produite.");
    }
};
